import React from "react"
import {Link} from "gatsby"
import "../styles/style.css"
import MyHelmet from "../components/my-helmet"

export default function Home () {
  return <div className={"flex flex-col justify-around h-full w-full"}>

    <div className={"h-full w-full flex flex-col items-center justify-start"}>
      <h1 className={"text-4xl m-4 sm:mt-0 sm:text-5xl text-gray-300"}>My projects</h1>
      <div className={"my-4 text-gray-300"}>★</div>
      <a className={'underline text-xl text-gray-300 sm:text-2xl'} rel={'noreferrer'} target={'_blank'} href={'//startupblink.com'}>StartupBlink Startup Ecosystem Map</a>
      <div className={"my-4 text-gray-300"}>★</div>
      <a className={'underline text-xl text-gray-300 sm:text-2xl'} rel={'noreferrer'} target={'_blank'} href={'//coronavirus.startupblink.com'}>Coronavirus Innovation Map</a>
      <div className={"my-4 text-gray-300"}>★</div>
      <a className={'underline text-xl text-gray-300 sm:text-2xl'} rel={'noreferrer'} target={'_blank'} href={'//altsh.ru'}>altsh.ru</a>
      <div className={"my-4 text-gray-300"}>★</div>
      <a className={'underline text-xl text-gray-300 sm:text-2xl'} rel={'noreferrer'} target={'_blank'} href={'//github.com/fcFn/Traymond'}>Traymond</a>
      <div className={"my-4 text-gray-300"}>★</div>
      <Link className={'text-xl text-gray-300 sm:text-2xl'} to={'/'}>Back</Link>
    </div>
  </div>
}

export function Head() {
  return (<MyHelmet>
  <title>Projects</title>
  <meta name="description"
        content="Peter Timoshevsky's projects"/>
  <meta name="og:description"
        content="Peter Timoshevsky's projects'"/>
  <meta name="og:title" content="Peter Timoshevsky"/>
</MyHelmet>)
}
